$fl:flex;
%btnChecked{
  width: 32px;
  height: 32px;
  background: linear-gradient(180deg, #89F09C 0%, #3CC755 100%);
  border-radius: 8px;
  vertical-align: baseline!important;
}
%btnUNChecked{
  background: #FFFFFF;
  border: 1px solid #F2F2F2;
  border-radius: 8px;
  width: 32px;
  height: 32px;
  vertical-align: baseline!important;
}

%closebtn{
  @extend %btnUNChecked;
    @extend %flex;
    justify-content: center;
    opacity: 0.5;
    transition: opacity 0.23s ease-in;
    cursor: pointer;
    img{
    width: 11px;
    height: 11px;
    }
    &:hover{
      opacity: 1;
    }
  }


%likeChecked{
  width: 32px;
  height: 32px;
  background: #FEF0F0;
  border-radius: 7px;
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(30px, 30px);
  display:$fl;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
%likeUnChecked{
  position: absolute;
  width: 32px;
  height: 32px;
  background: #FFFFFF;
  border: 1px solid #F8F8F8;
  box-sizing: border-box;
  border-radius: 7px;
  top: 0;
  left: 0;
  transform: translate(30px, 30px);
  display:$fl;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

%flex{
  display: flex;
  align-items: center;
}

@mixin fonts($fstyle,$fw,$fsize,$lh,$color){
  font-style: $fstyle;
font-weight: $fw;
font-size: $fsize;
line-height:$lh;
color: $color;
}

.wrapper{
  background: #FFFFFF;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
  border-radius: 20px;
  max-width: 1080px;
  margin: 50px auto;
  height: 1200px;
}


.content{
  padding: 45px 60px 73px 60px;
  &>*+*{
    margin-top:40px;
  }
}

.content_header{
  @extend%flex;
  justify-content: space-between;
}
.content_items{
  display: $fl;
  flex-wrap: wrap;
 column-gap: 40px;
 row-gap: 52px;
}

.error{
  position: fixed;
  top: 30px;
  left: 10px;
  width: auto;
  height: 70px;
  display: flex;
  align-items: center;
  background: #FFFFFF;
  padding: 0 10px;
  border: 1px solid grey;
  border-radius: 10px;
  z-index: 9;
  span{
    color: rgba(232, 95, 95, 0.897);
  }
}

.Nofavorite{
  @extend %flex;
  flex-direction: column;
  margin: auto;
  height: 500px;
  justify-content: center;
  img{
    width: 70px;
    height: 70px;
    margin-bottom: 24px;
  }
  h3{
    @include fonts(normal,600 ,24px,29px,#000000);
    text-align: center;
    margin-bottom: 11px;
  }
  p{
  @include fonts(normal,400 ,16px,19px,#000000); 
  text-align: center;
  opacity: 0.4;
  }
 
  }
  .Loading{
    position: absolute;
  }
  .content_header__Orders{
    @include fonts(normal,700 ,32px,39px,#000000); 
  }
