@import "./../App.scss";

.header{
    display:$fl ; 
   justify-content: space-between;
   /*padding-top: 43px;
   padding-left: 45px;
   padding-bottom: 45px;
   padding-right: 69px;*/
   padding:43px 69px 45px  45px   ;
   border-bottom: 1px solid #EAEAEA;
   
   
   }
   .header_Right{
     @extend%flex;
     &>*+*{
       margin-left: 30px;
     }
   }
   .header_Right__path1{
     position: relative;
     @extend%flex;
     img{
       cursor: pointer;
       margin-right: 10px;
       //background: #9DD558;
     }
     span{
       position: absolute;
       top: 0;
       left: 0;
       transform: translate(9px, -10px);
       z-index: 3;
       width: 5px;
       color: #9DD558;
   
     }
     &>*+*{
       margin-left: 10px;
     }
      }
   
   
   .header_Right__path2{
    
     &>*+*{
       margin-left: 36.3px
   }
   }
   
   
   .header_Left{
    
    a{
        @extend%flex;
     &>*+*{
       margin-left: 16px;
     }
     }
     img{
       width: 40px;
       height: 40px;
     }
     h1{
       @include fonts(normal,700,20px,24px, #000000);
       text-transform: uppercase;
   
   color: #000000;
     }
     p{
       @include fonts(normal,400,14px,17px,#9D9D9D)
     }
   }

   .f{
    animation:wrapper-opacity 1.5s ease-in-out!important ;
  }
   
   @keyframes wrapper-opacity{
     0%{
       opacity: 1;
     }
     /*25%{
       opacity: 0;
     }*/
     50%{
       opacity: 0;
     }
     /*75%{
       opacity: 0;
     }*/
     100%{
       opacity: 1;
     }
   }