
@import "./../App.scss";


.content__card{
    width: 210px;
    height: 260px;
    background: #FFFFFF;
    border: 1px solid #F3F3F3;
    border-radius: 40px;
    padding: 20px 23px 34px 27px;
    position: relative;
    transition:box-shadow 0.3s ease-in,transform 0.3s ease-in ;
&:hover{
    box-shadow: 0px 14px 30px rgba(0, 0, 0, 0.05);
    transform: translateY(-5px);
}
    span{
        @include fonts(normal,500,11px,13px,#BDBDBD);
text-transform: uppercase;
    }
    b{
        @include fonts(normal,700,14px,17px,#000000);
    }
    .btnUNChecked{
       @extend %btnUNChecked;
    img{
        vertical-align: baseline
        }
    }
    .unlike_wrapper{
       @extend %likeUnChecked;
        img{
            width: 14.4px;
            height: 12.8px;
        }
    }
    .like_wrapper{
        @extend %likeChecked;
        img{
            width: 14.4px;
            height: 12.8px;
        }
    }

}
.content__card_title{
    @include fonts(normal,400,13px,17px,#000000);
    margin-top:14px;
}
.content__card_subtitle{
    @include fonts(normal,400,14px,17px,#000000);
    margin-bottom:14px;
}

.content__card_priceHolder{
    @extend%flex;
    
    &>*+*{
        margin-left: 39px;
      }
} 
.btnChecked{
    @extend %btnChecked;
    img{
        vertical-align: baseline!important;
    }
}